body,html{
    height: 100%;
    width: 100%;
    
}

.cont{
    background-color: rgba(64, 52, 236, 0.473);
    border-radius: 20px;
    
}
@media screen and (max-width: 650px){
   .cont{
       width: 75%;
   }     
   .logo{
       width: 75vw;
       height: 12vh
   }
   .wr{
       width:75%
   }
   .bg{
       background-color: black;
       height: 100vh;
       
   }
}
@media screen and (min-width: 651px){
    .cont{
        width: 25%;
    }  
    .logo{
        width: 30vw;
        height: 12vh
    }
    .wr{
        width: 25%;
    } 
    .bg{
        background: url("../img/bg.jpg") no-repeat;
    
        background-size: 100vw 100vh;
        height: 100vh;
        
    }  
 }
 .correct{
     display: none;
     position: absolute;
     bottom: 35%;
     left: 9%;
 }

 .wrong{
    display: none;
    position: absolute;
    bottom: 35%;
    left: 9%;
}